body * {
  font-family: "Staatliches", cursive;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: rgb(228 225 224);
}
.card {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 500px;
  min-height: 250px;
  background: #fff;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.1);
  border-radius: 0px;
  transition: 0.5s;
}
.card:hover {
  box-shadow: 0 30px 70px rgba(0, 0, 0, 0.2);
}
.card .box {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  text-align: center;
  padding: 20px;
  box-sizing: border-box;
  width: 100%;
}
.card .box .img {
  width: 120px;
  height: 120px;
  margin: 0 auto;
  border-radius: 50%;
  overflow: hidden;
}
.card .box .img img {
  width: 100%;
  height: 100%;
}
.card .box h2 {
  font-size: 20px;
  color: #262626;
  margin: 20px auto;
}
.card .box h2 span {
  font-size: 14px;
  background: #e91e63;
  color: #fff;
  display: inline-block;
  padding: 4px 10px;
  border-radius: 15px;
}
.card .box p {
  color: #262626;
}
.card .box span {
  display: inline-flex;
}
.card .box ul {
  margin: 0;
  padding: 0;
}
.card .box ul li {
  list-style: none;
  float: left;
}
.card .box ul li a {
  display: block;
  color: #aaa;
  margin: 0 10px;
  font-size: 20px;
  transition: 0.5s;
  text-align: center;
}
.card .box ul li:hover a {
  color: #e91e63;
  transform: rotateY(360deg);
}

.btn-primary {
  text-transform: uppercase;
  -webkit-font-smoothing: subpixel-antialiased;
  background-color: rgb(233 30 99);
  /* top: -4px; */
  /* left: 0px; */
  border-radius: 4px;
  color: #ffffff;
  cursor: pointer;
  margin-top: 25px;
  width: 200px;
  transform: translate3d(-8px, 0px, 0px);
  transition: transform 0s ease 0s;
  font-weight: 500;
  /* box-shadow: 0 16px 26px -10px rgba(244, 67, 54, 0.56), 0 4px 25px 0px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(244, 67, 54, 0.2); */
  outline: none;
  outline-style: none;
  border: none;
  font-size: 18px;
  height: 40px;
}
.bold {
  font-weight: 400;
  color: #262626;
  font-size: 18px;
  line-height: 27px;
}

@media (max-width: 600px) {
  .card {
    width: 90%;
  }
}
